.card {
  width: 350px;
  background: rgba(82, 171, 255, 0.1);
  text-align: center;
  border-radius: 4px;
  margin: 0 10px;
  padding: 30px 0px;
  color: #f3f3f3;
  transition: all 0.2s;
  cursor: pointer;
}
.card:hover {
  transform: translateY(-10px) scale(1.03);
}
.card img {
  width: 200px;
  border-radius: 50%;
}
